* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

main {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  padding: 1rem;
}

h2 {
  margin-bottom: 20px;
  text-align: center;
}

label {
  margin: 1rem;
}

input {
  margin: 10px;
}

video {
  margin-top: 1rem;
  box-shadow: 0px 3px 21px -1px rgba(72, 71, 71, 0.85);
}
